html {
    scroll-behavior: smooth;
}
.navName {
    color: #8773f0; 
    text-decoration: none;
    font-size: 2.5rem;
    /* -webkit-text-stroke: .3px black; */
}
.navName:hover {
    color: black;
    transition: color 0.3s ease-in-out;
}
.navTabs {
    font-size: 1.1rem;
    font-weight: bold;
    color: black;
    border-radius: .8rem; 
    padding: .5rem; 
    border: 2px solid transparent;
    text-decoration: none;
    align-self: center;
}
.navTabs:hover {
    border: 2px solid black;
    transition: border 0.3s ease-in-out;
}
.projectsLink {
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: bold;
    color: #fcda68;
    cursor: pointer;
}
.projectsLink:hover {
    color: black;
    transition: color 0.3s ease-in-out;
}